/* eslint-disable */


export const displayMap = (locations) => {
    mapboxgl.accessToken = 'pk.eyJ1Ijoic21oYXJnbiIsImEiOiJjbTdubHN0emEwMmsyMm1zYm1iZHRjbmduIn0.8DULCsaBhd8GALweVe2sVg';
    const map = new mapboxgl.Map({
        container: 'map', // container ID
        style: 'mapbox://styles/smhargn/cm7nm35df002i01s8be059sy7',
        scrollZoom : false
        // center: [-118.113491,34.111745],
        // zoom : 9,
        // interactive : false,

    });

    const bounds = new mapboxgl.LngLatBounds();

    locations.forEach(loc => {

        const el = document.createElement('div');
        el.className = 'marker';

        new mapboxgl.Marker({
            element: el,
            anchor: 'bottom'
        }).setLngLat(loc.coordinates).addTo(map);

        // Add popıp
        new mapboxgl.Popup({
            offset: 30
        }).setLngLat(loc.coordinates)
            .setHTML(`<p>Day ${loc.day} : ${loc.description}</p>`)
            .addTo(map);

        bounds.extend(loc.coordinates);

    });

    map.fitBounds(bounds,{
        padding : {
            top:200,
            bottom:200,
            left:100,
            right:100
        }
    });
}



